
import XButton from 'vux/src/components/x-button/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'

export default {
name: 'DialogInput',
props: {
isShow: {
type: Boolean,
default: false,
required: true
},
inputItems: {
type: Array,
default: [],
required: false
}
},
components: {
XButton, XInput, Group
},
data () {
return {
values: [],
names: {_from: '发送地址:', _to: '接收地址:', _value: '交易金额:', owner_: '新合约拥有者:', _spender: '被授权地址:', _subtractedValue: '减少金额:', _owner: '操作地址:', mintedAmount: '增发数量:', _addedValue: '增加授权金额:', target: '操作地址:', freeze: '是否冻结:', address: '请输入地址:', uint256: '请输入数值:',bool:'1:是，0:否', psd: '交易密码:', password: '请输入密码:', _useAddress: '合约拥有者:', _contractAddress: '关联合约地址:', _defaultCoin: '设置奖励个数:', _months: '生日(月):', _mdays: '生日(日):', uint8: '请输入数字格式', _currentYearGet: '是否已发放:'
}
}
},
methods: {
closeMyself() {
this.$emit("on-close")
},
tradeBtn () {
var that = this
let values = that.values
that.$emit("trades-btn",{values:values})
that.values = []
}
}





}



